
*{
  margin: auto;
  padding: 0;
  box-sizing: border-box;
}
.App{
  margin-top: 50px;
  padding: 10px;
}
.wrapped {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem;
  border-bottom: 1px solid #ccc;
}

input[type="text"] {
  width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  border-radius: 4px;
  box-sizing: border-box;
  margin-bottom: 1rem;
}

button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  background-color: #4caf50;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;

}
.delete {
  background-color: red;
}
.delete:hover {
  background-color: red;
  box-shadow: transparent;
  padding: 5px;
}
button:hover {
  background-color: #45a049;
}

@media screen and (max-width: 600px) {
  .wrapped {
    flex-direction: column;
    align-items: flex-start;
  }

  input[type="text"] {
    margin-bottom: 0.5rem;
  }

  button {
    width: 100%;
    margin-top: 0.5rem;
  }
  .delete:hover {
    background-color: red;
    box-shadow: transparent;
    padding: 5px;
  }
}